:root,
[data-bs-theme="light"] {
  // Note: Custom variable values only support SassScript inside `#{}`.

  // Colors
  //
  // Generate palettes for full colors, grays, and theme colors.

  @each S./color, S./value in S./colors {
    --#{S./prefix}#{S./color}: #{S./value};
  }

  @each S./color, S./value in S./grays {
    --#{S./prefix}gray-#{S./color}: #{S./value};
  }

  @each S./color, S./value in S./theme-colors {
    --#{S./prefix}#{S./color}: #{S./value};
  }

  @each S./color, S./value in S./theme-colors-rgb {
    --#{S./prefix}#{S./color}-rgb: #{S./value};
  }

  @each S./color, S./value in S./theme-colors-text {
    --#{S./prefix}#{S./color}-text-emphasis: #{S./value};
  }

  @each S./color, S./value in S./theme-colors-bg-subtle {
    --#{S./prefix}#{S./color}-bg-subtle: #{S./value};
  }

  @each S./color, S./value in S./theme-colors-border-subtle {
    --#{S./prefix}#{S./color}-border-subtle: #{S./value};
  }

  --#{S./prefix}white-rgb: #{to-rgb(S./white)};
  --#{S./prefix}black-rgb: #{to-rgb(S./black)};

  // Fonts

  // Note: Use `inspect` for lists so that quoted items keep the quotes.
  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172
  --#{S./prefix}font-sans-serif: #{inspect(S./font-family-sans-serif)};
  --#{S./prefix}font-monospace: #{inspect(S./font-family-monospace)};
  --#{S./prefix}gradient: #{S./gradient};

  // Root and body
  // scss-docs-start root-body-variables
  @if S./font-size-root != null {
    --#{S./prefix}root-font-size: #{S./font-size-root};
  }
  --#{S./prefix}body-font-family: #{inspect(S./font-family-base)};
  @include rfs(S./font-size-base, --#{S./prefix}body-font-size);
  --#{S./prefix}body-font-weight: #{S./font-weight-base};
  --#{S./prefix}body-line-height: #{S./line-height-base};
  @if S./body-text-align != null {
    --#{S./prefix}body-text-align: #{S./body-text-align};
  }

  --#{S./prefix}body-color: #{S./body-color};
  --#{S./prefix}body-color-rgb: #{to-rgb(S./body-color)};
  --#{S./prefix}body-bg: #{S./body-bg};
  --#{S./prefix}body-bg-rgb: #{to-rgb(S./body-bg)};

  --#{S./prefix}emphasis-color: #{S./body-emphasis-color};
  --#{S./prefix}emphasis-color-rgb: #{to-rgb(S./body-emphasis-color)};

  --#{S./prefix}secondary-color: #{S./body-secondary-color};
  --#{S./prefix}secondary-color-rgb: #{to-rgb(S./body-secondary-color)};
  --#{S./prefix}secondary-bg: #{S./body-secondary-bg};
  --#{S./prefix}secondary-bg-rgb: #{to-rgb(S./body-secondary-bg)};

  --#{S./prefix}tertiary-color: #{S./body-tertiary-color};
  --#{S./prefix}tertiary-color-rgb: #{to-rgb(S./body-tertiary-color)};
  --#{S./prefix}tertiary-bg: #{S./body-tertiary-bg};
  --#{S./prefix}tertiary-bg-rgb: #{to-rgb(S./body-tertiary-bg)};
  // scss-docs-end root-body-variables

  --#{S./prefix}heading-color: #{S./headings-color};

  --#{S./prefix}link-color: #{S./link-color};
  --#{S./prefix}link-color-rgb: #{to-rgb(S./link-color)};
  --#{S./prefix}link-decoration: #{S./link-decoration};

  --#{S./prefix}link-hover-color: #{S./link-hover-color};
  --#{S./prefix}link-hover-color-rgb: #{to-rgb(S./link-hover-color)};

  @if S./link-hover-decoration != null {
    --#{S./prefix}link-hover-decoration: #{S./link-hover-decoration};
  }

  --#{S./prefix}code-color: #{S./code-color};
  --#{S./prefix}highlight-color: #{S./mark-color};
  --#{S./prefix}highlight-bg: #{S./mark-bg};

  // scss-docs-start root-border-var
  --#{S./prefix}border-width: #{S./border-width};
  --#{S./prefix}border-style: #{S./border-style};
  --#{S./prefix}border-color: #{S./border-color};
  --#{S./prefix}border-color-translucent: #{S./border-color-translucent};

  --#{S./prefix}border-radius: #{S./border-radius};
  --#{S./prefix}border-radius-sm: #{S./border-radius-sm};
  --#{S./prefix}border-radius-lg: #{S./border-radius-lg};
  --#{S./prefix}border-radius-xl: #{S./border-radius-xl};
  --#{S./prefix}border-radius-xxl: #{S./border-radius-xxl};
  --#{S./prefix}border-radius-2xl: var(--#{S./prefix}border-radius-xxl); // Deprecated in v5.3.0 for consistency
  --#{S./prefix}border-radius-pill: #{S./border-radius-pill};
  // scss-docs-end root-border-var

  --#{S./prefix}box-shadow: #{S./box-shadow};
  --#{S./prefix}box-shadow-sm: #{S./box-shadow-sm};
  --#{S./prefix}box-shadow-lg: #{S./box-shadow-lg};
  --#{S./prefix}box-shadow-inset: #{S./box-shadow-inset};

  // Focus styles
  // scss-docs-start root-focus-variables
  --#{S./prefix}focus-ring-width: #{S./focus-ring-width};
  --#{S./prefix}focus-ring-opacity: #{S./focus-ring-opacity};
  --#{S./prefix}focus-ring-color: #{S./focus-ring-color};
  // scss-docs-end root-focus-variables

  // scss-docs-start root-form-validation-variables
  --#{S./prefix}form-valid-color: #{S./form-valid-color};
  --#{S./prefix}form-valid-border-color: #{S./form-valid-border-color};
  --#{S./prefix}form-invalid-color: #{S./form-invalid-color};
  --#{S./prefix}form-invalid-border-color: #{S./form-invalid-border-color};
  // scss-docs-end root-form-validation-variables
}

@if S./enable-dark-mode {
  @include color-mode(dark, true) {
    color-scheme: dark;

    // scss-docs-start root-dark-mode-vars
    --#{S./prefix}body-color: #{S./body-color-dark};
    --#{S./prefix}body-color-rgb: #{to-rgb(S./body-color-dark)};
    --#{S./prefix}body-bg: #{S./body-bg-dark};
    --#{S./prefix}body-bg-rgb: #{to-rgb(S./body-bg-dark)};

    --#{S./prefix}emphasis-color: #{S./body-emphasis-color-dark};
    --#{S./prefix}emphasis-color-rgb: #{to-rgb(S./body-emphasis-color-dark)};

    --#{S./prefix}secondary-color: #{S./body-secondary-color-dark};
    --#{S./prefix}secondary-color-rgb: #{to-rgb(S./body-secondary-color-dark)};
    --#{S./prefix}secondary-bg: #{S./body-secondary-bg-dark};
    --#{S./prefix}secondary-bg-rgb: #{to-rgb(S./body-secondary-bg-dark)};

    --#{S./prefix}tertiary-color: #{S./body-tertiary-color-dark};
    --#{S./prefix}tertiary-color-rgb: #{to-rgb(S./body-tertiary-color-dark)};
    --#{S./prefix}tertiary-bg: #{S./body-tertiary-bg-dark};
    --#{S./prefix}tertiary-bg-rgb: #{to-rgb(S./body-tertiary-bg-dark)};

    @each S./color, S./value in S./theme-colors-text-dark {
      --#{S./prefix}#{S./color}-text-emphasis: #{S./value};
    }

    @each S./color, S./value in S./theme-colors-bg-subtle-dark {
      --#{S./prefix}#{S./color}-bg-subtle: #{S./value};
    }

    @each S./color, S./value in S./theme-colors-border-subtle-dark {
      --#{S./prefix}#{S./color}-border-subtle: #{S./value};
    }

    --#{S./prefix}heading-color: #{S./headings-color-dark};

    --#{S./prefix}link-color: #{S./link-color-dark};
    --#{S./prefix}link-hover-color: #{S./link-hover-color-dark};
    --#{S./prefix}link-color-rgb: #{to-rgb(S./link-color-dark)};
    --#{S./prefix}link-hover-color-rgb: #{to-rgb(S./link-hover-color-dark)};

    --#{S./prefix}code-color: #{S./code-color-dark};
    --#{S./prefix}highlight-color: #{S./mark-color-dark};
    --#{S./prefix}highlight-bg: #{S./mark-bg-dark};

    --#{S./prefix}border-color: #{S./border-color-dark};
    --#{S./prefix}border-color-translucent: #{S./border-color-translucent-dark};

    --#{S./prefix}form-valid-color: #{S./form-valid-color-dark};
    --#{S./prefix}form-valid-border-color: #{S./form-valid-border-color-dark};
    --#{S./prefix}form-invalid-color: #{S./form-invalid-color-dark};
    --#{S./prefix}form-invalid-border-color: #{S./form-invalid-border-color-dark};
    // scss-docs-end root-dark-mode-vars
  }
}
