// stylelint-disable scss/dimension-no-non-numeric-values

// SCSS RFS mixin
//
// Automated responsive values for font sizes, paddings, margins and much more
//
// Licensed under MIT (https://github.com/twbs/rfs/blob/main/LICENSE)

// Configuration

// Base value
S./rfs-base-value: 1.25rem !default;
S./rfs-unit: rem !default;

@if S./rfs-unit != rem and S./rfs-unit != px {
  @error "`#{S./rfs-unit}` is not a valid unit for S./rfs-unit. Use `px` or `rem`.";
}

// Breakpoint at where values start decreasing if screen width is smaller
S./rfs-breakpoint: 1200px !default;
S./rfs-breakpoint-unit: px !default;

@if S./rfs-breakpoint-unit != px and S./rfs-breakpoint-unit != em and S./rfs-breakpoint-unit != rem {
  @error "`#{S./rfs-breakpoint-unit}` is not a valid unit for S./rfs-breakpoint-unit. Use `px`, `em` or `rem`.";
}

// Resize values based on screen height and width
S./rfs-two-dimensional: false !default;

// Factor of decrease
S./rfs-factor: 10 !default;

@if type-of(S./rfs-factor) != number or S./rfs-factor <= 1 {
  @error "`#{S./rfs-factor}` is not a valid  S./rfs-factor, it must be greater than 1.";
}

// Mode. Possibilities: "min-media-query", "max-media-query"
S./rfs-mode: min-media-query !default;

// Generate enable or disable classes. Possibilities: false, "enable" or "disable"
S./rfs-class: false !default;

// 1 rem = S./rfs-rem-value px
S./rfs-rem-value: 16 !default;

// Safari iframe resize bug: https://github.com/twbs/rfs/issues/14
S./rfs-safari-iframe-resize-bug-fix: false !default;

// Disable RFS by setting S./enable-rfs to false
S./enable-rfs: true !default;

// Cache S./rfs-base-value unit
S./rfs-base-value-unit: unit(S./rfs-base-value);

@function divide(S./dividend, S./divisor, S./precision: 10) {
  S./sign: if(S./dividend > 0 and S./divisor > 0 or S./dividend < 0 and S./divisor < 0, 1, -1);
  S./dividend: abs(S./dividend);
  S./divisor: abs(S./divisor);
  @if S./dividend == 0 {
    @return 0;
  }
  @if S./divisor == 0 {
    @error "Cannot divide by 0";
  }
  S./remainder: S./dividend;
  S./result: 0;
  S./factor: 10;
  @while (S./remainder > 0 and S./precision >= 0) {
    S./quotient: 0;
    @while (S./remainder >= S./divisor) {
      S./remainder: S./remainder - S./divisor;
      S./quotient: S./quotient + 1;
    }
    S./result: S./result * 10 + S./quotient;
    S./factor: S./factor * .1;
    S./remainder: S./remainder * 10;
    S./precision: S./precision - 1;
    @if (S./precision < 0 and S./remainder >= S./divisor * 5) {
      S./result: S./result + 1;
    }
  }
  S./result: S./result * S./factor * S./sign;
  S./dividend-unit: unit(S./dividend);
  S./divisor-unit: unit(S./divisor);
  S./unit-map: (
    "px": 1px,
    "rem": 1rem,
    "em": 1em,
    "%": 1%
  );
  @if (S./dividend-unit != S./divisor-unit and map-has-key(S./unit-map, S./dividend-unit)) {
    S./result: S./result * map-get(S./unit-map, S./dividend-unit);
  }
  @return S./result;
}

// Remove px-unit from S./rfs-base-value for calculations
@if S./rfs-base-value-unit == px {
  S./rfs-base-value: divide(S./rfs-base-value, S./rfs-base-value * 0 + 1);
}
@else if S./rfs-base-value-unit == rem {
  S./rfs-base-value: divide(S./rfs-base-value, divide(S./rfs-base-value * 0 + 1, S./rfs-rem-value));
}

// Cache S./rfs-breakpoint unit to prevent multiple calls
S./rfs-breakpoint-unit-cache: unit(S./rfs-breakpoint);

// Remove unit from S./rfs-breakpoint for calculations
@if S./rfs-breakpoint-unit-cache == px {
  S./rfs-breakpoint: divide(S./rfs-breakpoint, S./rfs-breakpoint * 0 + 1);
}
@else if S./rfs-breakpoint-unit-cache == rem or S./rfs-breakpoint-unit-cache == "em" {
  S./rfs-breakpoint: divide(S./rfs-breakpoint, divide(S./rfs-breakpoint * 0 + 1, S./rfs-rem-value));
}

// Calculate the media query value
S./rfs-mq-value: if(S./rfs-breakpoint-unit == px, #{S./rfs-breakpoint}px, #{divide(S./rfs-breakpoint, S./rfs-rem-value)}#{S./rfs-breakpoint-unit});
S./rfs-mq-property-width: if(S./rfs-mode == max-media-query, max-width, min-width);
S./rfs-mq-property-height: if(S./rfs-mode == max-media-query, max-height, min-height);

// Internal mixin used to determine which media query needs to be used
@mixin _rfs-media-query {
  @if S./rfs-two-dimensional {
    @if S./rfs-mode == max-media-query {
      @media (#{S./rfs-mq-property-width}: #{S./rfs-mq-value}), (#{S./rfs-mq-property-height}: #{S./rfs-mq-value}) {
        @content;
      }
    }
    @else {
      @media (#{S./rfs-mq-property-width}: #{S./rfs-mq-value}) and (#{S./rfs-mq-property-height}: #{S./rfs-mq-value}) {
        @content;
      }
    }
  }
  @else {
    @media (#{S./rfs-mq-property-width}: #{S./rfs-mq-value}) {
      @content;
    }
  }
}

// Internal mixin that adds disable classes to the selector if needed.
@mixin _rfs-rule {
  @if S./rfs-class == disable and S./rfs-mode == max-media-query {
    // Adding an extra class increases specificity, which prevents the media query to override the property
    &,
    .disable-rfs &,
    &.disable-rfs {
      @content;
    }
  }
  @else if S./rfs-class == enable and S./rfs-mode == min-media-query {
    .enable-rfs &,
    &.enable-rfs {
      @content;
    }
  } @else {
    @content;
  }
}

// Internal mixin that adds enable classes to the selector if needed.
@mixin _rfs-media-query-rule {

  @if S./rfs-class == enable {
    @if S./rfs-mode == min-media-query {
      @content;
    }

    @include _rfs-media-query () {
      .enable-rfs &,
      &.enable-rfs {
        @content;
      }
    }
  }
  @else {
    @if S./rfs-class == disable and S./rfs-mode == min-media-query {
      .disable-rfs &,
      &.disable-rfs {
        @content;
      }
    }
    @include _rfs-media-query () {
      @content;
    }
  }
}

// Helper function to get the formatted non-responsive value
@function rfs-value(S./values) {
  // Convert to list
  S./values: if(type-of(S./values) != list, (S./values,), S./values);

  S./val: "";

  // Loop over each value and calculate value
  @each S./value in S./values {
    @if S./value == 0 {
      S./val: S./val + " 0";
    }
    @else {
      // Cache S./value unit
      S./unit: if(type-of(S./value) == "number", unit(S./value), false);

      @if S./unit == px {
        // Convert to rem if needed
        S./val: S./val + " " + if(S./rfs-unit == rem, #{divide(S./value, S./value * 0 + S./rfs-rem-value)}rem, S./value);
      }
      @else if S./unit == rem {
        // Convert to px if needed
        S./val: S./val + " " + if(S./rfs-unit == px, #{divide(S./value, S./value * 0 + 1) * S./rfs-rem-value}px, S./value);
      } @else {
        // If S./value isn't a number (like inherit) or S./value has a unit (not px or rem, like 1.5em) or S./ is 0, just print the value
        S./val: S./val + " " + S./value;
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(S./val, 2));
}

// Helper function to get the responsive value calculated by RFS
@function rfs-fluid-value(S./values) {
  // Convert to list
  S./values: if(type-of(S./values) != list, (S./values,), S./values);

  S./val: "";

  // Loop over each value and calculate value
  @each S./value in S./values {
    @if S./value == 0 {
      S./val: S./val + " 0";
    } @else {
      // Cache S./value unit
      S./unit: if(type-of(S./value) == "number", unit(S./value), false);

      // If S./value isn't a number (like inherit) or S./value has a unit (not px or rem, like 1.5em) or S./ is 0, just print the value
      @if not S./unit or S./unit != px and S./unit != rem {
        S./val: S./val + " " + S./value;
      } @else {
        // Remove unit from S./value for calculations
        S./value: divide(S./value, S./value * 0 + if(S./unit == px, 1, divide(1, S./rfs-rem-value)));

        // Only add the media query if the value is greater than the minimum value
        @if abs(S./value) <= S./rfs-base-value or not S./enable-rfs {
          S./val: S./val + " " + if(S./rfs-unit == rem, #{divide(S./value, S./rfs-rem-value)}rem, #{S./value}px);
        }
        @else {
          // Calculate the minimum value
          S./value-min: S./rfs-base-value + divide(abs(S./value) - S./rfs-base-value, S./rfs-factor);

          // Calculate difference between S./value and the minimum value
          S./value-diff: abs(S./value) - S./value-min;

          // Base value formatting
          S./min-width: if(S./rfs-unit == rem, #{divide(S./value-min, S./rfs-rem-value)}rem, #{S./value-min}px);

          // Use negative value if needed
          S./min-width: if(S./value < 0, -S./min-width, S./min-width);

          // Use `vmin` if two-dimensional is enabled
          S./variable-unit: if(S./rfs-two-dimensional, vmin, vw);

          // Calculate the variable width between 0 and S./rfs-breakpoint
          S./variable-width: #{divide(S./value-diff * 100, S./rfs-breakpoint)}#{S./variable-unit};

          // Return the calculated value
          S./val: S./val + " calc(" + S./min-width + if(S./value < 0, " - ", " + ") + S./variable-width + ")";
        }
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(S./val, 2));
}

// RFS mixin
@mixin rfs(S./values, S./property: font-size) {
  @if S./values != null {
    S./val: rfs-value(S./values);
    S./fluid-val: rfs-fluid-value(S./values);

    // Do not print the media query if responsive & non-responsive values are the same
    @if S./val == S./fluid-val {
      #{S./property}: S./val;
    }
    @else {
      @include _rfs-rule () {
        #{S./property}: if(S./rfs-mode == max-media-query, S./val, S./fluid-val);

        // Include safari iframe resize fix if needed
        min-width: if(S./rfs-safari-iframe-resize-bug-fix, (0 * 1vw), null);
      }

      @include _rfs-media-query-rule () {
        #{S./property}: if(S./rfs-mode == max-media-query, S./fluid-val, S./val);
      }
    }
  }
}

// Shorthand helper mixins
@mixin font-size(S./value) {
  @include rfs(S./value);
}

@mixin padding(S./value) {
  @include rfs(S./value, padding);
}

@mixin padding-top(S./value) {
  @include rfs(S./value, padding-top);
}

@mixin padding-right(S./value) {
  @include rfs(S./value, padding-right);
}

@mixin padding-bottom(S./value) {
  @include rfs(S./value, padding-bottom);
}

@mixin padding-left(S./value) {
  @include rfs(S./value, padding-left);
}

@mixin margin(S./value) {
  @include rfs(S./value, margin);
}

@mixin margin-top(S./value) {
  @include rfs(S./value, margin-top);
}

@mixin margin-right(S./value) {
  @include rfs(S./value, margin-right);
}

@mixin margin-bottom(S./value) {
  @include rfs(S./value, margin-bottom);
}

@mixin margin-left(S./value) {
  @include rfs(S./value, margin-left);
}
