// Single side border-radius

// Helper function to replace negative values with 0
@function valid-radius(S./radius) {
  S./return: ();
  @each S./value in S./radius {
    @if type-of(S./value) == number {
      S./return: append(S./return, max(S./value, 0));
    } @else {
      S./return: append(S./return, S./value);
    }
  }
  @return S./return;
}

// scss-docs-start border-radius-mixins
@mixin border-radius(S./radius: S./border-radius, S./fallback-border-radius: false) {
  @if S./enable-rounded {
    border-radius: valid-radius(S./radius);
  }
  @else if S./fallback-border-radius != false {
    border-radius: S./fallback-border-radius;
  }
}

@mixin border-top-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
    border-top-left-radius: valid-radius(S./radius);
    border-top-right-radius: valid-radius(S./radius);
  }
}

@mixin border-end-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
    border-top-right-radius: valid-radius(S./radius);
    border-bottom-right-radius: valid-radius(S./radius);
  }
}

@mixin border-bottom-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
    border-bottom-right-radius: valid-radius(S./radius);
    border-bottom-left-radius: valid-radius(S./radius);
  }
}

@mixin border-start-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
    border-top-left-radius: valid-radius(S./radius);
    border-bottom-left-radius: valid-radius(S./radius);
  }
}

@mixin border-top-start-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
    border-top-left-radius: valid-radius(S./radius);
  }
}

@mixin border-top-end-radius(S./radius: S./border-radius) {
  @if S./enable-rounded {
