// scss-docs-start color-mode-mixin
@mixin color-mode(S./mode: light, S./root: false) {
  @if S./color-mode-type == "media-query" {
    @if S./root == true {
      @media (prefers-color-scheme: S./mode) {
        :root {
          @content;
        }
      }
    } @else {
      @media (prefers-color-scheme: S./mode) {
        @content;
      }
    }
  } @else {
    [data-bs-theme="#{S./mode}"] {
      @content;
    }
  }
}
// scss-docs-end color-mode-mixin
